import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { QrAndBarCodeCheckerComponent } from './qr-and-bar-code-checker.component';

const routes: Routes = [
  {
    path: '',
    component: QrAndBarCodeCheckerComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class QrAndBarCodeCheckerRoutingModule { }
