import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { QrAndBarCodeChecker } from 'app/core/models/qrAndBarCodeChecker.model';

@Component({
  selector: 'app-qr-and-bar-code-checker',
  templateUrl: './qr-and-bar-code-checker.component.html',
  styleUrls: ['./qr-and-bar-code-checker.component.scss']
})
export class QrAndBarCodeCheckerComponent implements OnInit {

  qrAndBarCodeChecker = new QrAndBarCodeChecker();
  qrAndBarCodeCheckerForm: FormGroup;
  qrAndBarCodeCheckerTypeList = ['S1', 'S2'];
  qrAndBarCodeCheckerType = '';

  qrBarCodeValid: boolean = false;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit(): void {
    this.setFormValidator();
  }

  validate(): void {
    let endpoint = this.qrAndBarCodeCheckerType === 'S1' ? ENDPOINTS.s1QrCheck : ENDPOINTS.s2QrCheck;
    endpoint += this.qrAndBarCodeChecker.qrOrBarCode;

    this.apiService.save(endpoint, this.qrAndBarCodeChecker).subscribe(
      res => {
        this.qrBarCodeValid = true;
      }, err => {
        this.qrBarCodeValid = false;
        this.sweetAlertService.customErrorMessage('QR or Bar Code is Invalid. Please try again.');
      }
    );
  }

  setFormValidator(): void {
    this.qrAndBarCodeCheckerForm = this.formBuilder.group({
      applicationType: ['', Validators.required],
      qrOrBarCode: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.qrAndBarCodeChecker = new QrAndBarCodeChecker();
    this.qrAndBarCodeCheckerForm.markAsUntouched();
    this.qrAndBarCodeCheckerForm.markAsPristine();
  }

}
